.player-wrapper {
  position: relative;
  padding-top: 56.25%;
}

.player-wrapper-big {
  position: relative;
  height: 550px;
  width: 570px;
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
}

.heading {
  position: relative;
  width: 80%;
}

.title {
  position: relative;
  z-index: 1;
  /* margin-top: -0.6rem; */
  margin-top: -1%;
  padding: 6px;
  display: inline-flex;
  width: 100%;
  background-color: #06bfad;
  color: white;
}

.learn {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  /* margin-top: 4.5rem; */
  /* margin-top: 11vh; */
  padding-top: 0.5%;
  background-color: #fcfcfc;
  font-family: Montserrat-Regular;
  display: inline-block;
}

.sidenav {
  background-color: white;
  position: relative;
  /* z-index: 2; */
  /* top: 9rem; */
  left: 0px;
}

.sidenav-close {
  background-color: white;
  position: relative;
  /* z-index: 2; */
  /* top: 9rem; */
  left: -17rem;
}

.menu {
  width: 18rem;
  height: 700px;
  overflow-y: scroll;
  overflow-x: hidden;
  padding-top: 8px;
  padding-bottom: 20px;
}

.bar {
  box-shadow: 2px 3px 4px rgba(0, 0, 0, 0.5);
}

.main {
  margin-left: 0;
  height: 700px;
  overflow-y: scroll;
}

.main-closed {
  margin-left: -18rem;
  height: 700px;
  overflow-y: scroll;
}

.content-list {
  font-family: Montserrat-Bold;
}

@media only screen and (max-width: 1000px) {
  .main {
    margin-left: 0;
  }
  .main-closed {
    margin-left: -14rem;
  }
  .menu {
    width: 15rem;
  }
  .sidenav-close {
    left: -14.3rem;
  }
}

