@media only screen and (max-height: 800px) {
  .menu {
    height: 500px;
  }
  .main {
    height: 500px;
  }
  .main-closed {
    height: 500px;
  }
}

@media only screen and (max-height: 600px) {
  .menu {
    height: 330px;
  }
  .main {
    height: 330px;
  }
  .main-closed {
    height: 330px;
  }
}

@media only screen and (max-height: 400px) {
  .menu {
    height: 130px;
  }
  .main {
    height: 130px;
  }
  .main-closed {
    height: 130px;
  }
}

@media only screen and (max-width: 400px) {
  .main {
    margin-left: 0;
  }
  .main-closed {
    margin-left: -13rem;
  }
  .menu {
    width: 14rem;
  }
  .sidenav-close {
    left: -13.3rem;
  }
  .sidenav {
    left: 0px;
  }
}
